"use client";
import { motion, useScroll, useTransform } from "framer-motion";
import Link from "next/link";

export default function Navbar() {
  const { scrollY } = useScroll();
  const height = useTransform(scrollY, [0, 120], [72, 56]);
  const blur = useTransform(scrollY, [0, 120], [0, 12]);
  const blurPx = useTransform(blur, (b) => `blur(${b}px)`);
  const opacity = useTransform(scrollY, [0, 120], [1, 0.95]);

  return (
    <motion.header
      style={{ height }}
      className="fixed top-0 left-0 right-0 z-50"
    >
      <motion.nav style={{ backdropFilter: blurPx, opacity }} className="glass container-max flex items-center justify-between rounded-xl mt-3 py-2">
        <Link href="/" className="font-heading text-xl md:text-2xl text-emerald">
          <span className="text-gradient-gold">Qalby</span>Muslim
        </Link>
        <div className="flex items-center gap-4">
          <Link href="#features" className="text-emerald/80 hover:text-emerald">Features</Link>
          <Link href="#about" className="text-emerald/80 hover:text-emerald">About</Link>
          <Link href="https://qalbymuslim.com" className="text-emerald/80 hover:text-emerald">Visit</Link>
        </div>
      </motion.nav>
    </motion.header>
  );
}
